* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
    text-decoration: none;
}
html,
body {
    height: 100%;
    background-color: #c2c2d6;
    overflow: hidden;
}




@media screen and (max-width:1024px){
    :root{
        font-size:smaller
    }
    .calculator{
        width: 80%;
    }
}
@media screen and (max-width:576px){
    .calculator{
        margin-top: 50%;
        width: 100%;
    }
}