
.calculator {
    margin: auto;
    margin-top: 4rem;
    background-color: #000;
    border: 1px solid #47476b;
    width: 30%;
    padding: 0.5rem;
}

.keypads {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 4rem);
}
.keypads div {
    transition: all 0.2s;
    line-height: 4rem;
}
#clear {
    text-align: center;
    color: white;
    grid-column: 1 / 3;
    border: 0.01rem solid black;
    background-color: #ac3939;
}

.operation,
.number {
    color: white;
    text-align: center;
    background-color: #666666;
    border: 0.01rem solid black;
}
#subtract,
#add {
    grid-column: 4;
}
#equals {
    background-color: #004466;
    line-height: 8rem;
    grid-column: 4;
    grid-row: 4 / span 2;
}

#zero {
    grid-column: 1 / span 2;
}
.numbers {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
    grid-row: 2 / span 3;
    grid-column: 1 / span 3;
}
.number {
    background-color: #4d4d4d;
    color: #fff;
}
.keypads div:hover,
#clear:hover {
    color: black;
    border-color: #eeeeee;
}
